.leave-button {
  width: 32px;
  height: 32px;
  position: absolute;
  top: 6px;
  right: 16px;
  transform: rotate(45deg);
  cursor: pointer;

  @media (max-width: 760px) {
    width: 29px;
    height: 29px;
  }

  @media (max-width: 640px) {
    width: 24px;
    height: 24px;
    cursor: default;
  }

  .horizontal,
  .vertical {
    width: 100%;
    height: 7px;
    display: block;
    background-color: rgb(0, 206, 209);
    position: absolute;
    border-radius: 12px;
    top: 50%;

    @media (max-width: 640px) {
      height: 5px;
    }
  }

  .vertical {
    transform: rotate(90deg);
  }
}
